@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}
h1 {
    font-size: 50px;
    color: #000000;
    font-style: normal;
    font-weight: normal;
    font-feature-settings: "smcp";
    font-variant: small-caps;
    text-align: left;
    letter-spacing: 0.6px;
    line-height: 3px;
  }
  
  h2 {
    font-family: courier, "courier new", monospace;
    font-size: 25px;
    color: #000000;
    font-style: normal;
    font-weight: normal;
    font-feature-settings: "smcp";
    font-variant: small-caps;
    text-align: left;
    letter-spacing: 0.6px;
    line-height: 2px;
  }
  
  h3 {
    font-family: courier, "courier new", monospace;
    font-size: 20px;
    color: #000000;
    font-style: normal;
    font-weight: normal;
    font-feature-settings: "smcp";
    font-variant: small-caps;
    text-align: left;
    letter-spacing: 0.6px;
    line-height: 1.5px;
  }
  
  .contact {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 60px;
  }
  
  .contact-info {
    font-family: "Playfair Display", sans-serif;
    display: flex;
    flex-direction: column;
  }
  
  .tables {
    display: flex;
    justify-content: space-between;
  }
  
  .skills, .tools {
    flex: 1 1;
    margin: 10px;
    background-color: #f2f2f2;
    padding: 10px;
    border-radius: 5px;
  }
  
  .skills table, .tools table{
    width: 100%;
    border-collapse: collapse;
  }
  
  .skills td, .tools td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .even-row {
    background-color: #d6e6e6;
  }
  
  .odd-row {
    background-color: #ffffff;
  }
  
  .download-button {
    font-family: Georgia, 'Times New Roman', Times, serif;
    display: inline-block;
    padding: 10px 20px;
    margin-right: 80px;
    margin-top: 20px;
    background-color: black;
    color: white;
    text-align: center;
    text-decoration: none;
    font-size: 16px;
    border-radius: 5px;
  }
  
  .download-button:hover {
    background-color: #45a049;
  }
  
  .work-title {
    font-size: 40px;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .work-exp {
    margin: 10px;
    padding: 10px;
    border-radius: 5px;
  }
  
  .work-item {
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 5px;
  }

  .company-name {
    font-family: "Poppins", sans-serif;
    font-size: 25px;
    margin-top: 10px;
    margin-bottom: 25px;
  }
  
  .job-title {
    font-family: "Poppins", sans-serif;
    font-size: 20px;
    margin: 5;
  }

  .period {
    color: #4d4d4d;
    font-size: 20px;
    margin-top: 25px;
  }
  
  .job-description {
    width: 100%;
    font-size: 16px;
    margin-bottom: 10px;
  }

